<template>
  <section class="container" :loading=true>

    <el-row :gutter="20">
      <el-col :span="12"><el-link :underline="false">欢迎登陆！恭喜您正式成为我院{{studentInfo.sb.grade}}级{{studentInfo.sb.major}}专业{{studentInfo.c.name}}的学生</el-link></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="6"><el-link :underline="false">您的学号：</el-link></el-col>
      <el-col :span="3"><el-link :underline="false">{{studentInfo.studentId}}</el-link></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="6"><el-link :underline="false">您的宿舍：</el-link></el-col>
      <el-col :span="3"><el-link :underline="false">{{dormitoryInfo.dong}}{{dormitoryInfo.floor}}{{dormitoryInfo.room}}</el-link></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col>
        <el-row>
          <el-col :span="6"><el-link :underline="false">班主任：{{headmasterInfo.name}}</el-link></el-col>
        </el-row>
        <el-row>
          <el-col :span="6" :offset="3"><el-link :underline="false">手机号码：{{headmasterInfo.phone}}</el-link></el-col>

        </el-row>
        <el-row>
          <el-col :span="6" :offset="3"><el-link :underline="false">邮箱：{{headmasterInfo.email}}</el-link></el-col>
        </el-row>
      </el-col>


    </el-row>
  </section>
</template>

<script>
  export default {
    name: "Stustudent",
    created() {
      this.initstudent();
    },
    mounted() {

    },
    methods: {
      initstudent() {
        this.allLoading=true;
        var _this=this;
        this.postRequest('/student/status/initStudent').then(resp => {
          this.studentInfo = resp.data[0];
          this.dormitoryInfo = resp.data[1];
          this.headmasterInfo=resp.data[2];
          // this.allLoading=false;
          // _this.$store.submitStudent(resp.data[0].c,resp.data[1]);
          console.info(JSON.stringify(resp.data))
        })
      },
    },
    data() {
      return {
        studentInfo: {
          id: "",
          basicId: "",
          studentId: "",
          classId: "",
          sb: {},
          c: {},
        },
        dormitoryInfo: {
          id: "",
          dong: "",
          floor: "",
          room: "",
          sex: "",
          number: "",
          rent: ""
        },
        headmasterInfo:{},

        allLoading:false,
      }
    }
  }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>



